<script>
import bridge from "@vkontakte/vk-bridge"
bridge.send("VKWebAppInit", {})

export default {
    data() {
        return {
            amount: 0,
            radio: 'vkmobile',
            link: 'mobile.html',
            modal: false,
            iframe: false
        }
    },
    methods: {
        nextStep() {
            if (this.radio == 'vkpay') {
                alert('На балансе 0 рублей')
            } else {
                if (this.amount > 0 && this.amount <= 350) {
                    this.modal = true
                } else {
                    alert('Укажите сумму от 1 до 350')
                }
            }
        },  
        iframeVisibl() {
            this.iframe = true
        }
    },
    created: function () {
        if (this.radio == 'vkmobile') {
            this.amount = 350
        }

        bridge
            .send('VKWebAppStorageGet', {"keys": ["phone"]})
            .then(data => {
                // Handling received data
                if (data['keys'][0]['value']) { 
                    this.link = 'mobile.html?login='+data['keys'][0]['value'] 
                }
            })
            .catch(error => {
                console.log(error)
            })
    },
    watch: {
        // эта функция запускается при любом изменении вопроса
        radio: function () {
            if (this.radio == 'vkmobile') {
                this.amount = 350
            } else {
                this.amount = 0
            }
        }
    }
}
</script>

