<script>
import bridge from "@vkontakte/vk-bridge"
bridge.send("VKWebAppInit", {})

export default {
    data() {
        return {
            success: false,
            modalFinish: false
        }
    },
    created: function () {
        bridge
            .send('VKWebAppStorageGet', {"keys": ["success"]})
